import React, {useState, useEffect} from 'react';
import { useHistory } from 'react-router-dom';
import { useSelector } from 'react-redux';
import styled from 'styled-components';
import { vw } from '@/styles/utils';
import Page from '@/components/Page';
import RingsDeco from '@/components/Deco/RingsDeco';
import Graph from '@/components/Deco/Graph';
import AudioDescription from '@/components/AudioDescription';
import KeyPadPopup from '@/components/KeyPadPopup';
import Credit from '@/components/Credit';
import contentMatrix from '@/data/contentMatrix';
import { loader } from "@/plugins/components/Preloader";
import Content from '@/pages/Attract/elements/Content';
import {useDispatch } from 'react-redux';
import {sendKeypadToGA } from '@/redux/gaEvents';
import { wasKeypadUsed as wasKeypadUsedSelector } from '@/redux/app';
import {setSessionStartTime} from '@/redux/app';
import { isTimeoutPopupActiveSelector } from '@/redux/app';
import { tabable } from '@/utils/screenreader';

import { NO_SOUND, CREDIT_ICON } from '@/config/config';


export default function Attract() {
  const history = useHistory();
  const [activeCredit, setActiveCredit] = useState(false);
  const wasKeypadUsed = useSelector(wasKeypadUsedSelector);
  const [imageCount, setImageCount] = useState(0);
  const isTimeoutPopupActive = useSelector(isTimeoutPopupActiveSelector);
  const dispatch = useDispatch();




  useEffect(() => {
    const timer = setTimeout(() => {
      if(imageCount===1){
        setImageCount(0)
      }
      else{
        setImageCount(imageCount+1)
      }
    }, 6000);
    return () => clearTimeout(timer);
  }, [imageCount]);
  
// console.log(preload.preload);

  const handleClick = () => {
    dispatch(sendKeypadToGA());
    dispatch(setSessionStartTime(new Date()));
    history.push('/main');
  };

  const handleCreditToggle = (isActive) => {
    setActiveCredit(isActive);
  };



  return (
    <Root 
    data-testid="attract"
    src={loader.get(contentMatrix.attract.slideImages[imageCount]).src}

    >
      <RingsDeco style={{ top: vw(212), left: vw(111) }} type="orange" />
      <Graph style={{ bottom: vw(178), left: vw(119) }} type="orange" />

      <ContentWrapper>
        <Content/>
      </ContentWrapper>
      <AudioDescription 
        description={contentMatrix.attract.audioDescription} 
         autoFocus={true} 
        autoFocusInNonAD={!activeCredit}
        active={!wasKeypadUsed && !activeCredit}
      />
    <KeyPadPopup
      tabIndex={tabable(2, (!activeCredit && !isTimeoutPopupActive))} 
      handleClickContinue={handleClick}
      activeCredit={activeCredit}
    />
    <TouchContainer onClick={handleClick}/>
    <CreditButton 
      onClick={()=>{handleCreditToggle(true)}}
      src={loader.get(CREDIT_ICON).src}
      tabIndex={tabable(3, (!activeCredit && !isTimeoutPopupActive))} 
      aria-roledescription={contentMatrix.attract.creditButtonLabel}
    />
    <Credit 
      active={activeCredit}
      handleClick={()=>handleCreditToggle(false)}
    />
    <NoSound 
      src={loader.get(NO_SOUND).src}
      tabIndex={-1}
    />
    
    </Root>
  );
}

const Root = styled(Page)`
  color: ${({ theme }) => theme.color.tertiary};
  background-image:url(${props=>props.src});
  background-size: cover;
  transition: all 2s ease-in-out;
`;

const TouchContainer =styled.div`
position: absolute;
width: 100%;
height: 100%;
left: 0;
right: 0;
`;
const ContentWrapper = styled.div`
  position: absolute;
  top: ${vw(305)};
  left: ${vw(111)};
  text-align: left;
  width: ${vw(809)};
  height: ${vw(322)};
  h1 {
    padding-bottom: ${vw(29)};
    font-size: ${vw(110)};
    text-transform: capitalize;
    letter-spacing: ${vw(2.6)};
    line-height: ${vw(110)};
    text-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
    font-family: ${({ theme }) => theme.font.chakra};
    font-weight: 500;
    font-style: normal;
  }
 
`;

const NoSound = styled.img`
  position: absolute;
  bottom: ${vw(25)};
  left: ${vw(27)};



`;
const CreditButton = styled.button`
  position: absolute;
  bottom: ${vw(32.5)};
  right: ${vw(32)};
  width:${vw(140)};
  height:${vw(55)};
  background-image:url(${props=>props.src});
  background-position: center;
  background-repeat: no-repeat;
 
`;