import { LOCALHOST, TRACKING_ID, VIEW_EVENTS, HOTSPOT_EVENTS, KEYPAD_EVENTS, BUTTON_EVENTS, SESSION_DURATION_EVENTS } from '@/enums/Events';
import contentMatrix from '@/data/contentMatrix';
const views = contentMatrix.main.views;

// Constants
export const VIEW_GA__SEND = 'VIEWS_GA::SEND';
export const HOTSPOT_GA__SEND = 'HOTSPOTS_GA::SEND';
export const KEYPAD_GA__SEND = 'KEYPAD_GA::SEND';

// Constants
export const GA_ACTION_SAVE = 'GA_ACTION::SAVE';
export const GA_BUTTON_TYPE_SET = 'GA_BUTTON_TYPE::SET';


  //dispatched in Attract.js when handleClick triggered
  export const sendKeypadToGA = () =>(dispatch, getState)=>{
    const state = getState();
    const wasKeyPadUsedStatus = getWasKeypadUsed(state);
    
    dispatch(writeToJson(
      TRACKING_ID, 
      KEYPAD_EVENTS.CATEGORY,
      KEYPAD_EVENTS.ACTION,
      `${wasKeyPadUsedStatus ? KEYPAD_EVENTS.LABEL2 : KEYPAD_EVENTS.LABEL1}`,
      KEYPAD_EVENTS.VALUE
      ));
  };





    //dispatch buttons : startover, back, playagain
    export const sendButtonInfoToGA = () =>(dispatch, getState)=>{
      const state = getState();
      const buttonClicked = getButtonType(state);
      const didTimeOut = getDidTimeout(state);
    
      switch(buttonClicked){
        //dispatched in useResetHook
        case "startover":
          //action
          dispatch(sendSessionDurationToGA());
          dispatch(writeToJson(
            TRACKING_ID, 
            BUTTON_EVENTS.CATEGORY,
            BUTTON_EVENTS.ACTION,
            `${didTimeOut ? BUTTON_EVENTS.LABEL_TIMESOUT : BUTTON_EVENTS.LABEL_START_OVER}`,
            BUTTON_EVENTS.VALUE,
          ));
          break;
        default:
          break;
      }
  
     
    };
  //dispatched in VideoList
  export const sendViewToGA = () =>(dispatch, getState)=>{
    const state = getState();
    const currentViewIndex = getCurrentViewIndex(state);
    const isSidebarUsed = getIsSidebarUsed(state);

    dispatch(writeToJson(
      TRACKING_ID, 
      VIEW_EVENTS.CATEGORY,
      VIEW_EVENTS.ACTION,
      `${isSidebarUsed ? VIEW_EVENTS.LABEL2 : VIEW_EVENTS.LABEL1} - ${views[currentViewIndex].title}`,
      `${VIEW_EVENTS.VALUE}`,
  
      ));
  };


//dispatched in Look.js when handleClickHotspot is called
export const sendHotspotToGA = () =>(dispatch, getState)=>{
    const state = getState();
    const currentHotspotIndex = getCurrentHotspotIndex(state);
    const currentViewIndex = getCurrentViewIndex(state);
    
    dispatch(writeToJson(
      TRACKING_ID, 
      HOTSPOT_EVENTS.CATEGORY,
      HOTSPOT_EVENTS.ACTION,
      `${views[currentViewIndex].title} - ${views[currentViewIndex].hotspots[currentHotspotIndex].title}`,
      `${HOTSPOT_EVENTS.VALUE}`,
  
      ));
   

};
//start time triggered in Attract, duration called and calculated in useResetHook
export const sendSessionDurationToGA = () =>(dispatch, getState)=>{
  const state = getState();
  const sessionDuration = getSessionDuration(state);
  dispatch(writeToJson(
    TRACKING_ID, 
    SESSION_DURATION_EVENTS.CATEGORY,
    SESSION_DURATION_EVENTS.ACTION,
    `${SESSION_DURATION_EVENTS.LABEL} ${sessionDuration} seconds`,
    sessionDuration,
  ));
};

export const setButtonType =(buttonType)=>{
  return {
    type: GA_BUTTON_TYPE_SET,
    payload: buttonType ,
  };
}


//default function that writes to json file
export const writeToJson =(gaTrackingId,gaCategory, gaAction, gaLabel, gaValue)=>async(dispatch, getState)=>{
  await fetch(LOCALHOST, {
    method: 'POST',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json'
  },          
      body: JSON.stringify({
      id: null, 
      TrackingId: gaTrackingId,
      Category: gaCategory, 
      Action: gaAction,
      Label: gaLabel,
      Value: gaValue
    })
  }).catch((err)=>{console.log(err)});
};

// Initial State
const initialState = {
  actionInfo:{
    id: null, 
    TrackingId: TRACKING_ID,
    Category: 'null', 
    Action: 'null',
    Label: 'null',
    Value: 'null'
  },

  buttonType: 'none',
};

// Reducer
const reducer = (state = initialState, action = {}) => {
  const { payload } = action;
  switch (action.type) {
      case GA_ACTION_SAVE: 
        return {
          ...state,
          actionInfo: payload,
        }
        case GA_BUTTON_TYPE_SET: 
        return {
          ...state,
          buttonType: payload,
        }

    default:
      return state;
  }
};

export default reducer;

//Selectors
export const getCurrentViewIndex  = (state) => state.views.activeViewIndex;
export const getCurrentHotspotIndex  = (state) => state.hotspots.activeHotspotIndex;
export const getWasKeypadUsed = (state)=>state.app.wasKeypadUsed;
export const getIsSidebarUsed = (state)=>state.views.isSidebarUsed;
export const getDidTimeout = (state)=>state.app.didTimeout;
export const getSessionDuration = (state)=>state.app.sessionDuration;
export const getButtonType = (state)=>state.gaEvents.buttonType;