import React, {useState, useRef} from 'react';
import { useHistory } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
import StartOverButton from '@/components/StartOverButton';
import { vw } from '@/styles/utils';
import Page from '@/components/Page';
import AudioDescription from '@/components/AudioDescription';
import { setActiveView } from '@/redux/views';
import {sendViewToGA} from '@/redux/gaEvents';
import contentMatrix from '@/data/contentMatrix';
import KeypadVerticalNavList from '@/plugins/components/KeypadVerticalNavList';
import { Slide } from '@/components/Animation';
import { loader } from "@/plugins/components/Preloader";
import { tabable } from '@/utils/screenreader';
import { isTimeoutPopupActiveSelector } from '@/redux/app';
import { MAIN_BG, SELECTION_BUTTON } from '@/config/config';


const views = contentMatrix.main.views;
export default function Main() {
  const history = useHistory();
  const dispatch = useDispatch();
  const activeViewIndex = useSelector((state) => state.views.activeViewIndex);
  const isTimeoutPopupActive = useSelector(isTimeoutPopupActiveSelector);

  const parentRef = useRef();


  const handlekeyDown = (e)=>{
    if(activeViewIndex!==null){
      if(e.key==="Enter"){
        handleClickButton(activeViewIndex);
      };
    }
  };

  const handleOnFocusOnView=(index)=>{
    dispatch(setActiveView(index));
    dispatch(sendViewToGA());
  };

  const [clicked, setClicked] = useState(false);

  const handleClickButton = (index) => {
    if (!clicked) {
      setClicked(true);
      dispatch(setActiveView(index));
      history.push('/view');
    }
  };


  return (
    <Root                 
      src={loader.get(MAIN_BG).src}
    >
      <Wrapper
        ref={parentRef}
      >
      <Overlay src={require('@/assets/images/main/overlay.png')}/>
      <Slide direction="down" value={100}>
      <Content 
        tabIndex={tabable(2, !isTimeoutPopupActive)} 
        aria-label={`${contentMatrix.main.titleBodyAD}, ${contentMatrix.main.visitLinkText}`} 
        aria-roledescription=" "
      >
        <h1 >
        {contentMatrix.main.title}
        </h1>
        <h2>
          {contentMatrix.main.bodyText}
        </h2>
      </Content>
      </Slide>
      <List 
        tabIndex={tabable(4, !isTimeoutPopupActive)}
        aria-label={`${contentMatrix.main.groupAD}, ${contentMatrix.main.pressAD}`}
        aria-roledescription=" "
      >
        {views.map((view, index) => (
            <ButtonDiv 
            tabIndex={tabable(5, !isTimeoutPopupActive)} 
            onClick={() => handleClickButton(index)} 
            onKeyDown={handlekeyDown}  
            onFocus={()=>handleOnFocusOnView(index)} 
            aria-label={`${index+1} of ${views.length}, ${view.title}, ${view.main_alt}`}
            aria-roledescription=" "
            key={index}
            >
          <Slide delay={600}>
 
            <ButtonContainer>
          
              <Button
                data-testid={`button-${index}`}
                index={index}
                view={view}
                src={loader.get(view.thumb).src}
                tabIndex="-1"
              >
                <Gradient/>
                <h1>{view.title}</h1>
                <Oval
                  src={loader.get(SELECTION_BUTTON).src}
                  index={index}
                />
              </Button>
            </ButtonContainer>
            </Slide>

          </ButtonDiv >
 
        ))}
      </List>
      <Slide delay={1000}>
        <VisitText tabIndex="-1">
          {contentMatrix.main.visitLinkText}
        </VisitText>
      </Slide>

      <AudioDescription 
        description={contentMatrix.main.audioDescription} 
        autoFocus={true} 
        zindex={"999"} 
      />   
      <StartOverButton tabIndex={tabable(8, !isTimeoutPopupActive)}
/>
      </Wrapper>
    </Root>
  );
}

const Root = styled(Page)`
  display: flex;
  align-items: center;
  justify-content: center;
  color: ${({ theme }) => theme.color.tertiary};
  background-image: url(${props=>props.src});
  background-size: cover;
  background-repeat: no-repeat;

`;


const Wrapper = styled.div`


`;


const VisitText = styled.div`
    margin-left: ${vw(0)};
    margin-top: ${vw(880)};
    height: ${vw(31)};
    font-size: ${vw(22)};
    line-height: ${vw(24)};
    font-family: ${({ theme }) => theme.font.nunito};
    font-weight: normal;
    font-style: normal;
`;

const Content = styled.div`
  display: flex;
  position: absolute;
  text-align: left;
  top: ${vw(76)};
  left: ${vw(77)};
  h1 {
    width: ${vw(570)};
    height: ${vw(139)};
    font-size: ${vw(57)};
    text-transform: uppercase;
    letter-spacing: ${vw(0.76)};
    line-height: ${vw(72)};
  }
  h2 {
    margin-top: ${vw(5.5)};
    margin-left: ${vw(39)};
    width: ${vw(1006)};
    height: ${vw(147)};
    font-size: ${vw(28)};
    font-family: ${({ theme }) => theme.font.nunito};
    font-weight: normal;
    font-style: normal;
    line-height: ${vw(44)};
    span{
      font-weight: bold;
    }
  }
`;

const List = styled(KeypadVerticalNavList)`
  display: flex;
  width: ${vw(1775)};
  height: ${vw(650)};
  justify-content: space-between;
  position: absolute;
  top: ${vw(263)};
  left: 50%;
  transform: translateX(-50%);
`;

const ButtonContainer = styled.div`
  margin-top: ${vw(10)};
  margin-left: ${vw(5)};
  width: ${vw(549)};
  height: ${vw(638)};
  display: block;
  background: #c4caca;
  /* border: white solid 3px; */
  clip-path: polygon(
    0% 15%,
    0 0,
    15% 0%,
    94% 0,
    98% 5%,
    100% 7%,
    100% 85%,
    100% 100%,
    85% 100%,
    15% 100%,
    0 100%,
    0% 85%
  );
`;
const ButtonDiv= styled.div`
    width: ${vw(560)};
    height: ${vw(657)};
`;

const Button = styled.button`
  position: relative;
  width: ${vw(543)};
  height: 99%;
  left: ${vw(3)};
  top: ${vw(3)};
  background: #555;
  display: block;
  outline: none;
  pointer-events: none;
  color: ${({ theme }) => theme.color.tertiary};
  background-image: url(${(props) => props.src});
  background-size: ${vw(560)} ${vw(650)};
  background-repeat: no-repeat;
  background-position: center;
  clip-path: polygon(
    0% 15%,
    0 0,
    15% 0%,
    94% 0,
    98% 5%,
    100% 7%,
    100% 85%,
    100% 100%,
    85% 100%,
    15% 100%,
    0 100%,
    0% 85%
  );


  h1 {
    width: ${vw(487)};
    height: ${vw(117)};
    font-family: ${({ theme }) => theme.font.chakra};
    font-size: ${vw(43)};
    font-weight: 600;
    font-style: normal;
    letter-spacing: ${vw(1.07)};
    line-height: ${vw(58)};
    text-align: center;
    margin-top: ${vw(-120)};
    margin-left: auto;
    margin-right: auto;
  }
`;
const Oval = styled.img`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  height: ${vw(108)};
  width: ${vw(108)};
  display: block;
  margin-left: auto;
  margin-right: auto;
`;

const Overlay = styled.img`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  height:100%;
  width: 100%;
  display: block;
`;


const Gradient = styled.div`
  background-image: url(${require('@/assets/images/main/gradient.png')});
  background-size: cover;
  margin-top: ${vw(65)};
  width: 100%;
  height: 90%;
`;