import React, {useRef} from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { vw } from '@/styles/utils';
import { useSelector } from 'react-redux';
import contentMatrix from '@/data/contentMatrix';
import { loader } from '@/plugins/components/Preloader';
import { tabable } from '@/utils/screenreader';
import { isTimeoutPopupActiveSelector } from '@/redux/app';
import { ATTRACT_HAND, TAB_ARROW_LEFT, TAB_ARROW_RIGHT, TAB_TOGGLE_ICON } from '@/config/config';
const TAB = 9;
const ENTER = 13;
const UP = 38;
const DOWN = 40;

export default function HotspotTab({
  active,
  hotspot,
  onClickCloseTab,
  onClickFirstHotspot,
  onClickNext,
  onClickPrevious,
  activeView,
  activeHotspotIndex,
  hotspotTabContentGroupRef,
  hotspotTabCloseButtonRef,
  handleArrowBoxBlur,
}) {
  const isTimeoutPopupActive = useSelector(isTimeoutPopupActiveSelector);
  const prevArrowRef = useRef();
  const nextArrowRef = useRef();
  const selectedContentHotspotRef = useRef();
  const handleClickClose = () => {
    onClickCloseTab();
  };
  const handleClickFirstHotspot =(index)=>{
    onClickFirstHotspot(index);
  }

  const handleClickNext = () => {
    onClickNext();

  };
  const handleClickPrevious = () => {
    onClickPrevious();

  };

  const handleKeyDown = (e, element) =>{
   
    if(element==="container"){
      if(e.keyCode===TAB){
        if(e.shiftKey){
          console.log(e.shiftKey);
        }
        else{
          hotspotTabCloseButtonRef.current.focus();
        }
      }
      if(e.keyCode===UP){
        
        prevArrowRef.current.focus();
      }
      if(e.keyCode===DOWN){
        nextArrowRef.current.focus();

      }
    }
    if(element==="leftarrow"){
      e.preventDefault();
      console.log(e.keyCode);
      if(e.keyCode===TAB){
        if(e.shiftKey){
          selectedContentHotspotRef.current.focus();
        }
        else{
          hotspotTabCloseButtonRef.current.focus();
        }
      }
      if(e.keyCode===ENTER){
        onClickPrevious();
      }
    }
    if(element==="rightarrow"){
      e.preventDefault();
      console.log(e.keyCode);
      if(e.keyCode===TAB){
        if(e.shiftKey){
          selectedContentHotspotRef.current.focus();
        }
        else{
          hotspotTabCloseButtonRef.current.focus();
        }
      }
      if(e.keyCode===ENTER){
        onClickNext();
      }
    }
  }

  return (
    <Root active={active } >
      <AudioElement
        tabIndex={tabable(2, active && !isTimeoutPopupActive)} 
        aria-roledescription=" "
        aria-label= {activeHotspotIndex===null
          ? `${activeView.title}, ${activeView.viewHeader}, ${activeView.viewTextAD ? activeView.viewTextAD : activeView.viewText}` 
           : `,${hotspot.title} selected, ${hotspot.selectedAD}, ${contentMatrix.main.tabGroup.hotspotTabGroupAD}`}
        style={{ top: vw(10), left: vw(5), width: vw(630), height: activeHotspotIndex!==null ? vw(1040) : vw(700)}} 
        ref={hotspotTabContentGroupRef}
      />

 
      {(activeHotspotIndex === null && active ) ? (
        // This is initial view with no hotspot selected
        <Content 
        activeHotspotIndex={activeHotspotIndex} 
        active={true}
        touchIcon={loader.get(ATTRACT_HAND).src}
        customWidth={activeView.bodyWidth ? activeView.bodyWidth : null}

        >
          <h2>
            {activeView.title}
          </h2>
          <h1>
            {activeView.viewHeader}
          </h1>
          <p> 
            {activeView.viewText} 
          </p>
          <h3>
            {contentMatrix.main.sideTab[0]}
          </h3>
          <h5>
            {contentMatrix.main.sideTab[1]}
          </h5>

          <HotspotButton  
            tabIndex={tabable(3, active && !isTimeoutPopupActive)}
            aria-label={`${contentMatrix.main.sideTab[1]}, ${activeView.hotspots[0].title} button, ${contentMatrix.main.pressAD} `}
            aria-roledescription={" "}
            onClick={()=>{handleClickFirstHotspot(0)}}
            onFocus={handleArrowBoxBlur}
          >
          {activeView.hotspots[0].title}
          </HotspotButton>
        </Content>
      ) : (
        //This is view when hotspot is selected
        <Content 
        activeHotspotIndex={activeHotspotIndex} 
        active={activeHotspotIndex!==null}
        customWidth={hotspot.bodyWidth}
        >
          <AudioElement
            tabIndex={tabable(2, activeHotspotIndex!==null && !isTimeoutPopupActive)} 
            aria-label={`${hotspot.img_alt}, ${hotspot.title}, ${hotspot.bodyAD ? hotspot.bodyAD : hotspot.body}`}
            aria-roledescription={" "}
            style={{ top: vw(10), left: vw(5), width: vw(630), height: vw(865)}}
            ref={selectedContentHotspotRef} 
          />

          <Image 
            src={activeHotspotIndex!==null ? loader.get(hotspot.src).src : hotspot.src}  
            alt={hotspot.img_alt}  
            active={active}
          
          />
          <h1>
            {hotspot.title}</h1>
          <p> 
          {hotspot.body}</p>

          <ButtonSection >
        <span>
          {`${activeHotspotIndex+1} of ${activeView.hotspots.length}`}
        </span>
        {activeHotspotIndex!==null &&
        <ButtonsWrapper 
          onKeyDown={(e)=>handleKeyDown(e, "container")}
          tabIndex={tabable(3, active && !isTimeoutPopupActive)}
          aria-label={`${contentMatrix.main.tabGroup.hotspotTabButtonGroupAD}, ${activeHotspotIndex+1} of ${activeView.hotspots.length}, ${activeView.hotspots[activeHotspotIndex].title}`}
          aria-roledescription=" "
        >
          <NextPrevButton
            src={loader.get(TAB_ARROW_RIGHT).src}
            left={75}
            key={0}
            onClick={handleClickNext}
            onKeyDown={(e)=>handleKeyDown(e, "rightarrow")}
            aria-roledescription={`Next Button`}
            ref={nextArrowRef}
          />
          <NextPrevButton
            src={loader.get(TAB_ARROW_LEFT).src}
            left={0}
            key={1}
            onClick={handleClickPrevious}
            onKeyDown={(e)=>handleKeyDown(e, "leftarrow")}
            aria-roledescription={`Previous Button`}
            ref={prevArrowRef}

          />
        </ButtonsWrapper>
      }
      </ButtonSection>
        </Content>
      )}
      <Button 
        onClick={handleClickClose}
        ref={hotspotTabCloseButtonRef} 
        tabIndex={tabable((active ? 3 : 2),  !isTimeoutPopupActive)} 
        aria-label={active ? contentMatrix.main.tabGroup.closeButtonAD: contentMatrix.main.tabGroup.openButtonAD}
        aria-roledescription=" "
      >
        <Arrow
          src={loader.get(TAB_TOGGLE_ICON).src}
          active={active}
        />
      </Button>
     
    </Root>
  );
}

HotspotTab.propTypes = {
  active: PropTypes.bool,
  hotspot: PropTypes.object,
  onClickHotspot: PropTypes.func,
};
HotspotTab.defaultProps = {
  active: false,
  hotspot: {},
  onClickHotspot: null,
};

const Root = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: ${vw(635)};
  height: ${vw(1090)};
  opacity: 0.95;
  backdrop-filter: blur(${vw(5)});
  background: ${({ theme }) => theme.color.secondary};
  z-index: 998;
  transform: ${(props) =>
    props.active ? `translateX(0)` : 'translateX(-100%)'};
  transition: transform 0.5s;
`;

const Content = styled.div`
  display: block;
  position: absolute;
  opacity: ${props=>props.active ? 1 : 0};
  transition: opacity 2s ease-in-out;
  height: 92%;
  h1 {
    margin-top: ${props=>props.activeHotspotIndex===null ? vw(15) : vw(35.31)};
    margin-left: ${vw(45)};
    margin-right: auto;
    width: ${vw(535)};
    color: ${({ theme }) => theme.color.tertiary};
    font-size: ${vw(46)};
    line-height: 1;
    font-weight: bold;
  }
  h2 {
    margin-top: ${props=>props.activeHotspotIndex===null ? vw(189) : vw(65.31)};
    margin-left: ${vw(45)};
    margin-right: auto;
    width: ${vw(476)};
    color: ${({ theme }) => theme.color.primary};
    font-size: ${vw(28)};
    line-height: ${vw(30)};
    font-weight: bold;
  }
  p {
    margin-top: ${vw(30)};
    height: ${vw(192)};
    height: auto;
    width: ${props=>props.customWidth ? vw(props.customWidth) : vw(521)};
    margin-left: ${vw(45)};
    margin-right: auto;
    margin-bottom:${vw(42)};
    font-size: ${vw(22)};
    font-family: ${({ theme }) => theme.font.nunito};
    letter-spacing: ${vw(0.05)};
    line-height: ${vw(30)};
    color: ${({ theme }) => theme.color.tertiary};
  }

  h3 {
    display: ${(props) =>
    props.activeHotspotIndex == null ? 'block' : 'none'};
    margin-left: ${vw(43)};
    margin-right: auto;
    padding-left: ${vw(70)};
    padding-top: ${vw(20)};
    margin-bottom:${vw(116)};
    height: ${vw(64)};
    width: ${vw(470)};
    color: ${({ theme }) => theme.color.tertiary};
    font-size: ${vw(22)};
    line-height: ${vw(28)};
    font-weight: bold;
    background-image: url(${props=>props.touchIcon});
    background-size: ${vw(45)} ${vw(64)};
    background-position: 0% 50%;
    background-repeat: no-repeat;
  }

  h5{
    display: ${(props) =>
    props.activeHotspotIndex == null ? 'block' : 'none'};
    margin-left: ${vw(43)};
    margin-right: auto;
    margin-bottom:${vw(12.5)};
    height: ${vw(30)};
    width: ${vw(521)};
    color: ${({ theme }) => theme.color.tertiary};
    font-family: ${({ theme }) => theme.font.nunito};
    font-size: ${vw(22)};
    line-height: ${vw(30)};
    font-weight: normal;
  }
`;

const Image = styled.img`
  opacity: ${props=>props.active ? 1 : 0};
  height: ${vw(418)};
  width: ${vw(543)};
  margin-top: ${vw(44)};
  margin-left: ${vw(45)};
  box-sizing: border-box;
  border: ${vw(2)} solid ${({ theme }) => theme.color.tertiary};
`;

const Button = styled.button`
  position: absolute;
  display: flex;
  align-items: center;
  top: ${vw(502)};
  left: 100%;
  width: ${vw(62)};
  height: ${vw(95)};
  background: ${({ theme }) => theme.color.primary};
`;

const HotspotButton = styled.button`
  display: ${(props) =>
    props.activeHotspotIndex == null ? 'block' : 'none'};
    margin-left: ${vw(43)};
    margin-right: auto;
    height: ${vw(69)};
    width: auto;
    padding-left: ${vw(41.57)};
    padding-right:${vw(34.45)};
    border-radius: ${vw(12)};
    border: 3px solid ${({ theme }) => theme.color.primary};
    color: ${({ theme }) => theme.color.tertiary};
    font-size: ${vw(22)};
    line-height: ${vw(28)};
    font-family: ${({ theme }) => theme.font.chakra};
    font-weight: 600;
    :active{
      background: #B98A00;
    }
`;
const Arrow = styled.img`
  margin-left: auto;
  margin-right: auto;
  transform: ${(props) => (props.active ? '' : 'scaleX(-1)')};
  transition: transform 0.33s;
`;

const AudioElement = styled.div`
position: absolute;
  span{
      opacity: 0;
  }
`;

const ButtonSection = styled.div`
  position: absolute;
  bottom: ${vw(45)};
  left: ${vw(43)};
  width: ${vw(282)};
  height: ${vw(75)};
  background: ${({ theme }) => theme.color.primary};
  span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 90%;
    width: ${vw(100)};
    text-align: center;
    padding-top: ${vw(18)};
    color: #131415;
    font-family: ${({ theme }) => theme.font.industry};
    font-weight: 400;
    font-style: normal;
    font-size: ${vw(26)};
    line-height: ${vw(33)};
    white-space: nowrap;
    transition: all 0.8s;
  }
`;

const ButtonsWrapper = styled.div`
    height: 100%;
    width: 100%;
`;

const NextPrevButton = styled.button`
  position: absolute;
  width: 25%;
  height: ${vw(75)};
  left: ${(props) => props.left}%;
  background-image: url(${(props) => props.src});
  background-repeat: no-repeat;
  background-position: center;
  border-left: 1px solid #23221e;
  border-right: ${(props) => (props.left === 0 ? ' 1px solid  #23221e' : '')};
`;